import React, { Component } from 'react';
import Link from 'next/link';
import ModalVideo from 'react-modal-video';
import dynamic from 'next/dynamic';
import VisibilitySensor from "react-visibility-sensor";
const OwlCarousel = dynamic(import('react-owl-carousel3'));

const options = {
    loop: true,
    nav: true,
    dots: false,
    autoplayHoverPause: true,
    autoplay: true,
    items: 1,
    navText: [
        "<i class='icofont-rounded-left'></i>",
        "<i class='icofont-rounded-right'></i>"
    ],
}

class MainBannerTwo extends Component {
    // Video modal
    state = {
        isOpen: false,
    }
    openModal = () => {
        this.setState({isOpen: true})
    }

    // Home slides
    state = { 
        display: false,
        panel: true
    };

    componentDidMount(){ 
        this.setState({ display: true }) 
    }

    render() {
        return (
            <React.Fragment>
                {this.state.display ? <OwlCarousel 
                id="home" className="home-slides owl-carousel owl-theme"
                    {...options}
                >
                    <div className="main-banner item-bg-dat-le-nha-dan1">
                        <div className="d-table">
                            <div className="d-table-cell">
                                <div className="container">
                                    <VisibilitySensor>
                                        {({ isVisible }) => (
                                            <div className="main-banner-content text-center">
                                                <span
                                                    className={
                                                        isVisible ? "sub-title animated zoomIn opacityOne" : 'opacityZero'
                                                    }
                                                >
                                                    Đất Phú Hội
                                                </span>

                                                <h1
                                                className={
                                                    isVisible ? "animated fadeInUp opacityOne" : 'opacityZero'
                                                    }
                                                >
                                                    Khu đất giá rẻ tại xã Phú Hội
                                                </h1>

                                                <p
                                                    className={
                                                        isVisible ? "animated fadeInDown opacityOne" : 'opacityZero'
                                                    }
                                                >
                                                    Chỉ với 180tr/m dài, đường 12m, diện tích 12x80m² và 22x80m².
                                                </p>
                                                
                                                <div
                                                    className={
                                                        isVisible ? "animated fadeInDown opacityOne" : 'opacityZero'
                                                    }
                                                >
                                                    <div className="btn-box">
                                                        <Link href="#about">
                                                            <a className="btn btn-primary">
                                                                Tìm Hiểu Thêm
                                                            </a>
                                                        </Link>

                                                        <span>hoặc</span>

                                                        <Link href="#play-video">
                                                            <a
                                                                onClick={e => {e.preventDefault(); this.openModal()}}
                                                                className="video-btn popup-youtube"
                                                            > 
                                                                <i className="icofont-ui-play"></i> Xem Video
                                                            </a>
                                                        </Link>
                                                    </div>
                                                </div>
                                            </div>
                                        )}
                                    </VisibilitySensor>             
                                </div>
                            </div>
                        </div>

                        {/* Shape Images */}
                        <div className="shape1">
                            <img src={require("../../images/shapes/shape1.png")} alt="shape1" />
                        </div>
                        <div className="shape2 rotateme">
                            <img src={require("../../images/shapes/shape2.png")} alt="shape2" />
                        </div>
                        <div className="shape3 rotateme">
                            <img src={require("../../images/shapes/shape3.png")} alt="shape3" />
                        </div>
                        <div className="shape4">
                            <img src={require("../../images/shapes/shape4.png")} alt="shape4" />
                        </div>
                    </div>
                    <div className="main-banner item-bg-dat-le-nha-dan2">
                        <div className="d-table">
                            <div className="d-table-cell">
                                <div className="container">
                                    <VisibilitySensor>
                                        {({ isVisible }) => (
                                            <div className="main-banner-content text-center">
                                                <span
                                                    className={
                                                        isVisible ? "sub-title animated zoomIn opacityOne" : 'opacityZero'
                                                    }
                                                >
                                                    Đất Phú Hội
                                                </span>

                                                <h1
                                                className={
                                                    isVisible ? "animated fadeInUp opacityOne" : 'opacityZero'
                                                    }
                                                >
                                                    Khu đất giá rẻ tại xã Phú Hội
                                                </h1>

                                                <p
                                                    className={
                                                        isVisible ? "animated fadeInDown opacityOne" : 'opacityZero'
                                                    }
                                                >
                                                    Chỉ với 200tr/m dài, đường Bê Tông 6m, diện tích 18x50m²
                                                </p>
                                                
                                                <div
                                                    className={
                                                        isVisible ? "animated fadeInDown opacityOne" : 'opacityZero'
                                                    }
                                                >
                                                    <div className="btn-box">
                                                        <Link href="#about">
                                                            <a className="btn btn-primary">
                                                                Tìm Hiểu Thêm
                                                            </a>
                                                        </Link>

                                                        <span>hoặc</span>

                                                        <Link href="#play-video">
                                                            <a
                                                                onClick={e => {e.preventDefault(); this.openModal()}}
                                                                className="video-btn popup-youtube"
                                                            > 
                                                                <i className="icofont-ui-play"></i> Watch Video
                                                            </a>
                                                        </Link>
                                                    </div>
                                                </div>
                                            </div>
                                        )}
                                    </VisibilitySensor>             
                                </div>
                            </div>
                        </div>

                        {/* Shape Images */}
                        <div className="shape1">
                            <img src={require("../../images/shapes/shape1.png")} alt="shape1" />
                        </div>
                        <div className="shape2 rotateme">
                            <img src={require("../../images/shapes/shape2.png")} alt="shape2" />
                        </div>
                        <div className="shape3 rotateme">
                            <img src={require("../../images/shapes/shape3.png")} alt="shape3" />
                        </div>
                        <div className="shape4">
                            <img src={require("../../images/shapes/shape4.png")} alt="shape4" />
                        </div>
                    </div>
                </OwlCarousel> : ''}
 
                {/* If you want to change the video need to update below videoID */}
                <ModalVideo 
                    channel='youtube' 
                    isOpen={this.state.isOpen} 
                    videoId='ySVi5sjb7bU' 
                    onClose={() => this.setState({isOpen: false})} 
                />
            </React.Fragment>
        );
    }
}

export default MainBannerTwo;